.todo-list-item {
    display    : flex;
    align-items: center;
}

.todo-list-item-label {
    margin-right: auto;
    cursor      : pointer;
}

.todo-list-item .btn {
    margin-left: 5px;
    min-width  : 32px;
}

.todo-list-item.done .todo-list-item-label {
    text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
    color      : steelblue;
    font-weight: bold;
}
